import { Component, OnInit } from '@angular/core';
import { PolicyService } from 'src/app/policy.service';
import { AppComponent } from 'src/app//app.component';

@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css']
})
export class LogInComponent implements OnInit {

public username:any;
public password:any;
  constructor(private policyService: PolicyService,public app:AppComponent) { }
public success:any =false;
  ngOnInit() {
  }

  fuck(){
    console.log('here')
    this.policyService.getPolicies().subscribe((data : any[])=>{
        console.log(data);
      data.forEach(x=>{
        console.log(x)
        if(x.username ==  this.username && x.Password == this.password){
         this.success=true;
         console.log(this.success)
         this.app.inside = true;
         this.app.message = 'Login Successful'
         document.getElementById('msg').setAttribute("class", "text-success");
         document.getElementById('btn').click();}
         this.app.url="/dashboard"
      })


      if(this.success == false){
        this.app.message = 'Login unSuccessful check your credentials'
        document.getElementById('msg').setAttribute("class", "text-danger");
        document.getElementById('btn').click();}

    })
  }

}
